<template>
  <div class="otp-section">
    <div class="row">
      <div class="col-12 col-md-12 mt-3">
        <div class="card">
          <div class="p-3 ml-5 mt-4 mr-5 p-20">
            <p>
              OTP has been Send to your Mobile
              <span class="text-primary">({{phoneNumber}})</span> and your registered Email ID
              <span class="text-primary">({{this.manufacturerDto.contact.email}})</span>.
              <br />Please Enter OTP here
            </p>
            <div class="form-row col-md-6">
              <div class="form-group" :class="{ 'form-group--error': $v.smsOTP.$error }">
                <div class="form-group col-md-8">
                  <label for="inputEmail4">Enter Phone OTP</label>
                  <input
                    type="number"
                    class="form-control rounded"
                    id="inputEmail4"
                    placeholder="Phone OTP"
                    v-model.number="smsOTP"
                  />
                </div>
                <div v-if="otpScreenErrors">
                  <span class="text-danger" v-if="!$v.smsOTP.required">SMS OTP is required</span>
                </div>
              </div>
              <div class="form-group" :class="{ 'form-group--error': $v.emailOTP.$error }">
                <div class="form-group col-md-8">
                  <label for="inputPassword4">Enter Email OTP</label>
                  <input
                    type="number"
                    class="form-control rounded"
                    id="inputEmail4"
                    placeholder="Email OTP"
                    v-model.number="emailOTP"
                  />
                </div>
                <div v-if="otpScreenErrors">
                  <span class="text-danger" v-if="!$v.emailOTP.required">Email OTP is required</span>
                </div>
              </div>
            </div>
            <div class="form-row col-md-6">
              <div class="form-group col-md-12">
                <button
                  class="btn btn-primary float-right mb-4 pl-5 pr-5 p-2 mt-3 ml-3"
                  name="save"
                  value="SUBMIT"
                  v-on:click="verifyOTP();"
                  type="button"
                >SUBMIT</button>
                <button
                  class="btn btn-outline-primary float-right mb-4 pl-5 pr-5 p-2 mt-3 mr-3 ml-5"
                  name="save"
                  value="RESEND OTP"
                  v-on:click="resendOTP();"
                  type="button"
                >RESEND OTP</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { required } from "vuelidate/lib/validators";
import toast from "@/toast";
export default {
  data() {
    return {
      manufacturerDto: {},
      phoneNumber: "",
      emailOTP: "",
      smsOTP: "",
      otpScreenErrors: false
    };
  },
  created() {
    this.getmanufacturerDto;
  },
  computed: {
    getmanufacturerDto() {
      return this.loadManufacturerDto();
    }
  },
  validations: {
    emailOTP: { required },
    smsOTP: { required }
  },
  methods: {
    async loadManufacturerDto() {
      this.manufacturerDto = this.$store.getters.manufacturerDto;
      this.phoneNumber =
        this.manufacturerDto.contact.phone.substring(0, 2) +
        "********" +
        this.manufacturerDto.contact.phone.substring(
          this.manufacturerDto.contact.phone.length - 2
        );
      return this.manufacturerDto;
    },
    async verifyOTP() {
      this.$v.$touch();
      this.otpScreenErrors =
        this.$v.emailOTP.$anyError || this.$v.smsOTP.$anyError;
      if (!this.otpScreenErrors) {
        var objOTP = {
          smsCode: this.smsOTP,
          emailCode: this.emailOTP,
          mfId: this.manufacturerDto.mfId
        };
        await this.$store.dispatch("ManufacturerOTPStatusAsync", objOTP);
        if (this.$store.state.verifyOTPStatus === true) {
          toast.success("OTP Verified!..");
          this.$router.push({
            name: "MfActive",
            params: { mfId: this.manufacturerDto.mfId }
          });
        } else
          toast.error(
            "OTP is wrong or expried!..Try <b>Resend OTP</b> to new get OTP"
          );
      }
    },
    async resendOTP() {
      await this.$store.dispatch("resendOTPAsync", this.manufacturerDto.mfId);
      if (this.$store.state.resendOTPStatus === true)
        toast.success("OTP Sent!..");
    }
  }
};
</script>